// @ts-nocheck
import React, { useEffect, useState, useRef } from 'react';
import { Card, Button } from "@material-ui/core";
import { DAppClient } from "@airgap/beacon-sdk";

import Anim from "./assets/anim.png";
import knight from "./assets/knight.gif";
import monk from "./assets/monk.gif";
import priestess from "./assets/priestess.gif";
import card from "./assets/card.png";
import twitter from "./assets/twitter.png";
import gmail from "./assets/gmail.png";
import beacon from "./assets/beacon.svg";
import { PollingSubscribeProvider, TezosToolkit } from '@taquito/taquito'

import {
    BeaconWallet
} from '@taquito/beacon-wallet'



const Tezos = new TezosToolkit('https://mainnet.api.tez.ie')
const wallet = new BeaconWallet({
    name: 'hicetnunc.xyz',
    preferblackNetwork: 'mainnet',
})
Tezos.setWalletProvider(wallet)

const dAppClient = new DAppClient({ name: "Beacon Docs" });

export const Collection = () => {

    const [activeAccount, setActiveAccount] = useState();
    const [synced, setSynced] = useState("sync");
    const [showUnsync, setShowUnsync] = useState(false);
    const isMobile = window.innerWidth <= 500;

    const [elementals, setElementals] = useState([]);
    const [selectedCard, setSelectedCard] = useState(-1);
    const selectedCardRef = useRef();

    const [priestessCount, setPriestessCount] = useState(0);
    const [knightCount, setKnightCount] = useState(0);
    const [hashashinCount, setHashashinCount] = useState(0);
    const [monkCount, setMonkCount] = useState(0);
    const [bladekeeperCount, setBladekeeperCount] = useState(0);

    useEffect(() => {
        async function getAcc() {
            setActiveAccount(await dAppClient.getActiveAccount());
            if (activeAccount) {
                setSynced(
                    activeAccount.address.slice(0, 6) +
                    "..." +
                    activeAccount.address.slice(32, 36)
                );
                setShowUnsync(true);

            } else {
                setSynced("sync");
                setShowUnsync(false);
            }
        }
        getAcc();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [activeAccount]);

    async function unsync() {
        setActiveAccount(await dAppClient.getActiveAccount());
        if (activeAccount) {
            // User already has account connected, everything is ready
            dAppClient.clearActiveAccount().then(async () => {
                setActiveAccount(await dAppClient.getActiveAccount());
                setSynced("sync");
                setShowUnsync(false);
            });
        }
    }

    async function sync() {
        setActiveAccount(await dAppClient.getActiveAccount());
        //Already connected
        if (activeAccount) {
            setSynced(activeAccount.address);
            setShowUnsync(true);

            return activeAccount;
        }
        // The user is not synced yet
        else {
            try {
                console.log("Requesting permissions...");
                const permissions = await dAppClient.requestPermissions();
                setActiveAccount(await dAppClient.getActiveAccount());
                console.log("Got permissions:", permissions.address);
                setSynced(permissions.address);
                setShowUnsync(true);

            } catch (error) {
                console.log("Got error:", error);
            }
        }
    }


    useEffect(() => {
        async function fetchGraphQL(operationsDoc, operationName, variables) {
            let result = await fetch('https://api.fxhash.xyz/graphql', {
                method: 'POST',
                body: JSON.stringify({
                    query: operationsDoc,
                    variables: variables,
                    operationName: operationName,
                }),
                headers: {
                    "Content-Type": "application/json"
                }
            })

            var ress = await result.json();
            return ress;
        }

        async function fetchCollection(addr) {
            const { errors, data } = await fetchGraphQL(
                query_collection,
                'Query',
                {
                    "id": addr,
                    "skip": 0,
                    "take": 20,
                    "filters": {},
                    "sort": {
                        "id": "DESC"
                    }
                }
            )
            if (errors) {
                console.error(errors)
            }
            console.log(data)
            const result = data ? data.user.objkts : null;
            let col = result.map(({ issuer, metadata, assigned, id }) => ((issuer.author.id === "tz2DNkXjYmJwtYceizo3LwNVrqfrguWoqmBE" && issuer.name === "Batch 2 Test Collection" && assigned) ?
                (metadata.attributes[0].value) : null));

            let priestess = 0;
            let monk = 0;
            let knight = 0;
            let blade = 0;
            let Hashashin = 0;
            for (let index = 0; index < col.length; index++) {
                if (col[index] === "Priestess") {
                    priestess++;
                } else if (col[index] === "Monk") {
                    monk++;
                } else if (col[index] === "Knight") {
                    knight++;
                } else if (col[index] === "Blade") {
                    blade++;
                } else if (col[index] === "Hashashin") {
                    Hashashin++;
                }

            }
            console.log(col);
            setPriestessCount(priestess);
            setMonkCount(monk);
            setKnightCount(knight);
            setBladekeeperCount(blade);
            setMonkCount(monk);

        }
        if (activeAccount)
            fetchCollection(activeAccount.address);

    }, [activeAccount]);


    return (
        <div>


            {/*
            <div style={{ marginTop: "40vh" }}>
                <Card style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <div >
                        <b> collected Priestess {priestessCount}/4 </b>
                        <br></br>
                        <br></br>
                        <b>collected Knight {knightCount}/4 </b>
                        <br></br>
                        <br></br>
                        <b> collected Hashashin {hashashinCount}/4 </b>
                        <br></br>
                        <br></br>
                        <b>collected Monk {monkCount}/4 </b>
                        <br></br>
                        <br></br>
                        <b>collected Bladekeeper {bladekeeperCount}/4 </b>
                    </div>
                </Card>
                </div>*/}

            <div
                style={{ fontFamily: "Alagard", color: "white" }}
            >

                {showUnsync &&
                    <div style={{ position: "absolute", display: "flex", alignItems: "center", top: 0, right: 0 }}>
                        {activeAccount && activeAccount.address && activeAccount.address.slice(0, 6) + "..." + activeAccount.address.slice(32, 36)}

                        <Button
                            style={{ marginLeft: 10, fontFamily: "Alagard", color: "white" }}
                            onClick={() => { unsync() }}
                        >
                            UNSYNC
                        </Button>
                    </div>
                }


                {!showUnsync && <>
                    <h1 style={{ textAlign: "center", marginBottom: 40, marginTop: 60 }}>Welcome to ELEMENTALS</h1>

                    <div className="pixelart" style={{ textAlign: "center", width: "100%" }}>
                        {
                            <img src={Anim} style={{ width: isMobile ? "100%" : "50%" }} />
                        }
                    </div>

                    <h2 style={{ textAlign: "center", marginTop: 40 }}>Mint, Battle, Win</h2>



                    <div
                        style={{ fontFamily: "Inconsolata", color: "white", textAlign: "center", marginTop: 30 }}
                    >
                        Choose your method
                        <div style={{ display: "flex", justifyContent: "center", marginTop: 30 }}>
                            <Button
                                onClick={() => { window.open("https://wallet.kukai.app/"); }}
                            >
                                <img src={gmail} style={{ height: 50 }} />
                            </Button>
                            <Button
                                onClick={() => { sync() }}
                            >
                                <img src={beacon} style={{ height: 50 }} />
                            </Button>
                            <Button
                                onClick={() => { window.open("https://wallet.kukai.app/"); }}
                            >
                                <img src={twitter} style={{ height: 50 }} />
                            </Button>
                        </div>
                    </div>
                </>
                }

                {showUnsync &&
                    <>
                        <h1 style={{ textAlign: "center", marginBottom: 40, marginTop: 60 }}>ELEMENTALS</h1>
                        <h3 style={{ textAlign: "center", marginTop: 30 }}>preview</h3>
                        <div style={{ display: "flex", justifyContent: "center", marginTop: 20 }}>

                            <iframe
                                width={isMobile ? window.innerWidth : "500"}
                                height={isMobile ? window.innerWidth : "500"}
                                src="https://ipfs.io/ipfs/QmSarDqHQv2d1jfEszAi8Amv2b8PWghJA1EmAeef4NQt5s/?fxhash=ooWoY61u2r3uev2j5M4g3BnSVDKY86Pk6jGzWGmbmfbBQ1ZarQj"
                                sandbox="allow-scripts allow-same-origin"
                                allow="accelerometer; camera; gyroscope; microphone; xr-spatial-tracking;">
                            </iframe>
                        </div>
                        <div style={{ display: "flex", justifyContent: "center", marginTop: 30 }}>
                            <Button
                                variant="contained"
                                color="primary"
                                onClick={() => {

                                    Tezos.wallet
                                        .at('KT1BJC12dG17CVvPKJ1VYaNnaT5mzfnUTwXv')
                                        .then((contract) => {
                                            console.log(`collecting it ....`);
                                            return contract.methods.mint(12204).send();
                                        })
                                        .then((op) => {
                                            console.log(`Waiting for ${op.hash} to be confirmed...`);
                                            return op.confirmation(3).then(() => op.hash);
                                        })
                                        .then((hash) => {
                                            console.log(`Operation injected: https://tzstats.com/${hash}`);
                                        })
                                        .catch((error) => {
                                            console.log(`Error: ${JSON.stringify(error, null, 2)}`);
                                        });

                                }}
                            >
                                Mint Elemental 1 êœ©
                            </Button>
                        </div>

                        <h3 style={{ textAlign: "center", marginTop: 30 }}>inventory</h3>
                        <div className="background-container" style={{ overflowY: 'auto', backgroundColor: "#330033" }}>
                            <div className="background-icon-list" >
                                {elementals &&
                                    elementals.map((id) => (
                                        <div
                                            style={{ padding: 10 }}
                                        >

                                            <Button

                                                onClick={() => {
                                                    setSelectedCard(0);
                                                }}
                                            >
                                                <img src={card} width={isMobile ? window.innerWidth / 2 : "300"} height={isMobile ? window.innerWidth / 2 : "300"}></img>
                                            </Button>
                                        </div>
                                    ))
                                }
                            </div>
                        </div>

                        {selectedCard !== -1 &&
                            <div style={{ display: "flex", justifyContent: "center", marginTop: 20 }}>
                                <iframe
                                    width={isMobile ? window.innerWidth / 2 : "500"}
                                    height={isMobile ? window.innerWidth / 2 : "500"}
                                    src="https://ipfs.io/ipfs/QmSarDqHQv2d1jfEszAi8Amv2b8PWghJA1EmAeef4NQt5s/?fxhash=ooWoY61u2r3uev2j5M4g3BnSVDKY86Pk6jGzWGmbmfbBQ1ZarQj"
                                    sandbox="allow-scripts allow-same-origin"
                                    allow="accelerometer; camera; gyroscope; microphone; xr-spatial-tracking;">
                                </iframe>
                                <div style={{ display: "flex", marginLeft: 20, height: isMobile ? window.innerWidth / 2 : "500", flexDirection: "column", justifyContent: "center" }}>
                                    <div style={{ minHeight: isMobile ? window.innerWidth / 10 : 80, display: "flex", alignItems: "center" }}>
                                        Attack: XXX
                                    </div>
                                    <div style={{ minHeight: isMobile ? window.innerWidth / 10 : 80, display: "flex", alignItems: "center" }}>
                                        Health: XXX
                                    </div>
                                    <div style={{ minHeight: isMobile ? window.innerWidth / 10 : 80, display: "flex", alignItems: "center" }}>
                                        Attack1: XXX
                                    </div>
                                    <div style={{ minHeight: isMobile ? window.innerWidth / 10 : 80, display: "flex", alignItems: "center" }}>
                                        Attack2: XXX
                                    </div>
                                    <div style={{ minHeight: isMobile ? window.innerWidth / 10 : 80, display: "flex", alignItems: "center" }}>
                                        Attack3: XXX
                                    </div>
                                    <div style={{ minHeight: isMobile ? window.innerWidth / 10 : 80, display: "flex", alignItems: "center" }}>
                                        AttackSP: XXX
                                    </div>
                                </div>

                            </div>

                        }
                        {selectedCard !== -1 && <><div style={{ display: "flex", justifyContent: "center", marginTop: 20, marginBottom: 20 }} >
                            <Button
                                variant="contained"
                                color="primary"
                                onClick={() => {
                                    //setStartGame(true)
                                }}
                            >
                                BATTLE !
                            </Button>

                        </div>
                            <div ref={selectedCardRef}></div>
                        </>
                        }
                    </>
                }


            </div >
        </div>
    );
}


const query_collection = `
query Query($id: String!, $take: Int, $skip: Int, $sort: ObjktsSortInput, $filters: ObjktFilter) {
    user(id: $id) {
      id
      objkts(take: $take, skip: $skip, sort: $sort, filters: $filters) {
        id
        assigned
        rarity
        iteration
        owner {
          id
          name
          flag
          avatarUri
          __typename
        }
        issuer {
          name
          flag
          author {
            id
            name
            flag
            avatarUri
            __typename
          }
          __typename
        }
        name
        metadata
        createdAt
        offers {
          id
          price
          __typename
        }
        __typename
      }
      __typename
    }
  }
  
      `
