import Unity, { UnityContext } from "react-unity-webgl";
import { DAppClient } from "@airgap/beacon-sdk";
import React, { useEffect, useState } from 'react';
import { Button, makeStyles, Avatar, IconButton, createStyles, Theme } from "@material-ui/core";
import fx from "./assets/fx.png";
import fist from "./assets/fist.png";

const dAppClient = new DAppClient({ name: "Beacon Docs" });

const unityContext = new UnityContext({
    loaderUrl: "buildUnity/Build/buildUnity.loader.js",
    dataUrl: "buildUnity/Build/buildUnity.data.unityweb",
    frameworkUrl: "buildUnity/Build/buildUnity.framework.js.unityweb",
    codeUrl: "buildUnity/Build/buildUnity.wasm.unityweb",
});

const useStyles = makeStyles((theme) =>
    createStyles({
        root: {
            display: 'flex',
        },
        sizeBig: {
            width: theme.spacing(12),
            height: theme.spacing(12),
        },
        sizeSmall: {
            width: theme.spacing(6),
            height: theme.spacing(6),
        },
        sizeVerySmall: {
            width: theme.spacing(3),
            height: theme.spacing(3),
        },
    }),
);

export const Demo = () => {
    const classes = useStyles();
    const [activeAccount, setActiveAccount] = useState();
    const [synced, setSynced] = useState("sync");
    const [showUnsync, setShowUnsync] = useState(false);
    const isMobile = window.innerWidth <= 500;
    const isMobileLandscape = window.innerHeight <= 500;


    const [elementals, setElementals] = useState(false);

    useEffect(() => {
        async function getAcc() {
            setActiveAccount(await dAppClient.getActiveAccount());
            if (activeAccount) {
                setSynced(
                    activeAccount.address.slice(0, 6) +
                    "..." +
                    activeAccount.address.slice(32, 36)
                );
                setShowUnsync(true);

            } else {
                setSynced("sync");
                setShowUnsync(false);
            }
        }
        getAcc();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [activeAccount]);

    async function unsync() {
        setActiveAccount(await dAppClient.getActiveAccount());
        if (activeAccount) {
            // User already has account connected, everything is ready
            dAppClient.clearActiveAccount().then(async () => {
                setActiveAccount(await dAppClient.getActiveAccount());
                setSynced("sync");
                setShowUnsync(false);
            });
        }
    }

    async function sync() {
        setActiveAccount(await dAppClient.getActiveAccount());
        //Already connected
        if (activeAccount) {
            setSynced(activeAccount.address);
            setShowUnsync(true);

            return activeAccount;
        }
        // The user is not synced yet
        else {
            try {
                console.log("Requesting permissions...");
                const permissions = await dAppClient.requestPermissions();
                setActiveAccount(await dAppClient.getActiveAccount());
                console.log("Got permissions:", permissions.address);
                setSynced(permissions.address);
                setShowUnsync(true);

            } catch (error) {
                console.log("Got error:", error);
            }
        }
    }

    useEffect(() => {
        async function fetchGraphQL(operationsDoc, operationName, variables) {
            let result = await fetch('https://api.fxhash.xyz/graphql', {
                method: 'POST',
                body: JSON.stringify({
                    query: operationsDoc,
                    variables: variables,
                    operationName: operationName,
                }),
                headers: {
                    "Content-Type": "application/json"
                }
            })

            var ress = await result.json();
            return ress;
        }

        async function fetchCollection(addr) {
            const { errors, data } = await fetchGraphQL(
                query_collection,
                'Query',
                {
                    "id": addr,
                    "skip": 0,
                    "take": 40,
                    "filters": {},
                    "sort": {
                        "id": "DESC"
                    }
                }
            )
            if (errors) {
                console.error(errors)
            }
            console.log(data)
            const nfts = data ? data.user.objkts : null;
            console.log(nfts)
            let filtered = nfts.filter(function (nft) {
                return nft.issuer.author.id === "tz2HDnLqawEFd863vw8SvE4h1X8Cw6g1Xaqs" && nft.issuer.name === "Elementals, NFT Battler, Game Pieces" && nft.assigned
            })

            console.log(filtered);
            setElementals(filtered);
        }
        if (activeAccount)
            fetchCollection(activeAccount.address);

    }, [activeAccount]);


    return (
        <div>
            <div
                className="top-right"
                style={{ position: "relative", display: "flex", alignItems: "center", justifyContent: "right" }}
            >
                <div style={{ display: "flex", alignItems: "center", backgroundColor: "white", borderRadius: 10 }}>


                    {showUnsync && (
                        <Button
                            size={"small"}
                            title={"unsync"}
                            onClick={() => {
                                unsync();
                            }}
                        >
                            <u>unsync</u>{" "}
                        </Button>
                    )}

                    {showUnsync && <div> | </div>}
                    <Button
                        title={"sync"}
                        size={"small"}
                        onClick={async () => {
                            await sync();
                        }}
                    >
                        <u>{synced}</u>{" "}
                    </Button>
                </div>
            </div>
            {showUnsync &&
                <div style={{ display: "flex", justifyContent: "center" }}>
                    <Unity
                        unityContext={unityContext}
                        //matchWebGLToCanvasSize={true}
                        style={{ width: isMobile ? "100vw" : "80vw", height: isMobile ? "auto" : "80vh" }}
                    />
                </div>
            }
            {!showUnsync &&
                <div style={{ width: "99vw", height: "100vh", overflow: "hidden", fontFamily: "Alagard", color: "white", textAlign: "center" }}>
                    <h1>
                        Looks like you dont have any elementals you can mint via fxhash or our page.
                    </h1>


                    <IconButton
                        onClick={() => {
                            window.open("https://www.fxhash.xyz/generative/slug/elementals-nft-battler-game-pieces", "_blank")
                        }}
                    >
                        <Avatar variant="rounded" src={fx} alt={"fxhash"} className={classes.sizeBig} />

                    </IconButton>

                    <IconButton
                        onClick={() => {
                            window.open("https://elementals.run/mint")
                        }}
                    >
                        <Avatar variant="rounded" src={fist} alt={"fist"} className={classes.sizeBig} />

                    </IconButton>
                </div>
            }

            <div className="panel" style={{ display: "flex", width: "100%", overflowY: "auto", backgroundColor: "#330033", position: "absolute", bottom: 0 }}>
                {elementals &&
                    elementals.map(({ metadata, id }) => (
                        <IconButton
                            key={id}
                            onClick={() => {
                                let forge = (metadata.attributes[0].value + "." + metadata.attributes[1].value + "." + metadata.attributes[2].value + "." + metadata.attributes[3].value + "." + metadata.attributes[4].value + "." + metadata.attributes[5].value + "." + metadata.attributes[6].value);

                                unityContext.send("GameManager", "setCharacter", forge);
                            }}
                        >
                            <Avatar variant="rounded" src={HashToURL(metadata.displayUri, 'IPFS')} alt={id} className={isMobileLandscape ? classes.sizeSmall : classes.sizeBig} />

                        </IconButton>
                    ))
                }
            </div>

        </div >

    );
}


const query_collection = `
query Query($id: String!, $take: Int, $skip: Int, $sort: ObjktsSortInput, $filters: ObjktFilter) {
    user(id: $id) {
      id
      objkts(take: $take, skip: $skip, sort: $sort, filters: $filters) {
        id
        assigned
        rarity
        iteration
        owner {
          id
          name
          flag
          avatarUri
          __typename
        }
        issuer {
          name
          flag
          author {
            id
            name
            flag
            avatarUri
            __typename
          }
          __typename
        }
        name
        metadata
        createdAt
        offers {
          id
          price
          __typename
        }
        __typename
      }
      __typename
    }
  }
  
      `

const HashToURL = (hash, type) => {
    // when on preview the hash might be undefined.
    // its safe to return empty string as whatever called HashToURL is not going to be used
    // artifactUri or displayUri
    if (hash === undefined) {
        return ''
    }

    switch (type) {
        case 'HIC':
            return hash.replace('ipfs://', 'https://pinata.hicetnunc.xyz/ipfs/')
        case 'CLOUDFLARE':
            return hash.replace('ipfs://', 'https://cloudflare-ipfs.com/ipfs/')
        case 'PINATA':
            return hash.replace('ipfs://', 'https://gateway.pinata.cloud/ipfs/')
        case 'IPFS':
            return hash.replace('ipfs://', 'https://ipfs.io/ipfs/')
        case 'DWEB':
            return hash.replace('ipfs://', 'http://dweb.link/ipfs/')
        default:
            console.error('please specify type')
            return hash
    }
}